@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap);
*{box-sizing:border-box;margin:0;padding:0;font-family:"Poppins",sans-serif}body{margin:0.4rem auto;max-width:1296px;color:#282c34;background-color:#f7f7f7;padding:14px}li{list-style-type:none}a{text-decoration:none;color:#282c34;cursor:pointer}a:visited{color:#282c34}.navigation{margin:26px 0;display:flex}.navigation a{margin:10px;padding:10px;position:relative}.navigation .nav-active::after{content:"";width:85%;height:5px;background:#61dafb;position:absolute;bottom:1px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.Card{position:center}.Card img{display:block;margin-left:auto;margin-right:auto;height:70vh;width:70vw}.Card input[type=text]{padding:12px 20px;margin:8px 0;box-sizing:border-box;width:80%}.Card button{background-color:#e89910;padding:10px 0px;width:20%}.Card button:hover{opacity:1}.Score{position:center}.Score input[type=text]{padding:12px 20px;margin:8px 0;box-sizing:border-box;width:100%}.Score button{padding:10px 0px}.Score button:hover{opacity:1}.Score .yes{background-color:#0be345}.Score .no{background-color:#d90909}.grid{display:grid}.grid .timer{direction:rtl}.Classement{position:center}.Classement ol{line-height:180%}

